.additional-card {
  display: grid;
  grid-template-columns: auto 50px;
  padding-top: 12px;

  @media (min-width: @tablet-width) {
    grid-template-columns: auto 150px 95px;
    padding: 24px 36px 0;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: auto 245px 82px 244px;
    align-items: center;
    padding: 0;
    padding-top: 15px;
  }
}

.additional-card__title {
  grid-column: 1 / -1;
  margin: 0;
  margin-bottom: 13px;
  margin-left: 1px;

  font-family: @font-family-header;
  font-size: 16px;
  line-height: 16px;
  font-weight: @font-weight-regular;
  color: @alter-header-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    grid-column: 1 / 2;
    margin-bottom: 0;

    font-size: 20px;
    line-height: 24px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / 2;

    line-height: 21px;
  }
}

.additional-card__pack,
.additional-card__price {
  margin: 0;
  margin-bottom: 15px;

  font-family: @font-family-baze;
  font-size: 14px;
  line-height: 14px;
  font-weight: @font-weight-regular;
  color: @baze-color;

  @media (min-width: @tablet-width) {
    align-self: end;

    text-align: right;
  }

  @media (min-width: @desktop-width) {
    align-self: center;
    padding-top: 8px;

    text-align: left;
  }
}

.additional-card__pack {
  grid-column: 1 / 2;

  @media (min-width: @tablet-width) {
    grid-column: 2 / 3;
    margin-bottom: 0;

    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 / 3;
  }
}

.additional-card__price {
  grid-column: 2 / 3;

  text-align: right;

  @media (min-width: @tablet-width) {
    grid-column: 3 / 4;
    margin-bottom: 0;

    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 3 / 4;

    text-align: left;
  }
}

.additional-card__button {
  grid-column: 1 / -1;
  padding: 10px;

  @media (min-width: @tablet-width) {
    margin-top: 22px;

    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 4 / 5;
    margin-top: 0;
  }
}
