.welcome {
  background-image: url("/img/can-food-mobile.png"),
    url("/img/cat-promo-mobile.png"),
    linear-gradient(@primary-color 71%, #ffffff 29%);
  background-repeat: no-repeat;
  background-position: bottom 43px center, top center;
  background-size: 280px auto, 100%, 100%;

  @media (min-resolution: 2dppx) {
    background-image: url("/img/can-food-mobile@2x.png"),
      url("/img/cat-promo-mobile@2x.png"),
      linear-gradient(@primary-color 72%, #ffffff 25%);
    background-size: 280px auto, 100%, 100%;
  }

  @media (min-width: @tablet-width) {
    background-image: url("/img/can-food-tablet.png");
    background-position: bottom center;
    background-size: 709px auto;
    background-color: @alter-color;

    @media (min-resolution: 2dppx) {
      background-image: url("/img/can-food-tablet@2x.png");
      background-size: 709px auto;
    }
  }

  @media (min-width: @desktop-width) {
    background-color: rgba(255, 255, 255, 0);
    background-image: url("/img/can-food-desktop.png");
    background-repeat: no-repeat;
    background-position: right 23vw top 12px;
    background-size: 552px auto;

    @media (min-resolution: 2dppx) {
      background-image: url("/img/can-food-desktop@2x.png");
      background-size: 552px auto;
    }
  }
}

@media (min-width: @desktop-width) {
  .welcome::before {
    position: absolute;
    top: 0;
    left: 50vw;
    z-index: -10;

    width: 50%;
    height: 694px;

    background-color: #64a63b;
    background-image: url("../img/cat-promo-desktop.png");
    background-repeat: no-repeat;
    background-position: left top;
    background-size: auto;

    content: "";

    @media (min-resolution: 2dppx) {
      background-image: url("/img/cat-promo-desktop@2x.png");
      background-size: auto 694px;
    }
  }
}

.welcome__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 480px;
  box-sizing: border-box;
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    justify-content: flex-start;
    align-items: flex-start;
    min-height: 859px;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    min-height: 647px;
  }
}

.welcome__title {
  margin: 0;
  margin-bottom: 25px;

  text-align: center;
  color: @alter-color;

  @media (min-width: @tablet-width) {
    margin-bottom: 41px;
    padding-left: 86px;

    text-align: left;
    color: @header-color;
  }

  @media (min-width: @desktop-width) {
    padding-top: 93px;
    padding-left: 80px;
  }
}

.welcome__slogan {
  margin: 0;

  font-family: @font-family-header;
  font-size: 14px;
  line-height: 14px;
  font-weight: @font-weight-regular;
  text-align: center;
  color: @alter-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    padding-left: 88px;

    font-size: 20px;
    line-height: 20px;
    color: @header-color;
  }

  @media (min-width: @desktop-width) {
    padding-left: 80px;
  }
}

.welcome__button {
  margin-top: auto;

  font-size: 14px;
  line-height: 18px;

  @media (min-width: @tablet-width) {
    width: 244px;
    margin-top: 49px;
    margin-left: 89px;
    padding: 17px;

    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 51px;
    margin-left: 80px;
  }
}
