.catalog-card {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  align-content: start;
  box-sizing: border-box;
  width: 100%;
  min-width: 280px;
  min-height: 185px;
  padding: 0 20px;
  padding-top: 20px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    min-width: 324px;
    min-height: 506px;
    padding: 0 38px;
  }

  @media (min-width: @desktop-width) {
    min-width: 245px;
    min-height: 514px;
    padding: 0 38px 0 39px;
  }
}

.catalog-card__link--image {
  position: relative;

  grid-column: 1 / 2;
  grid-row: 1 / 3;
  min-height: 112px;

  text-align: center;

  &:hover {
    opacity: 60%;
    cursor: pointer;
  }

  &:focus {
    .focus();
  }

  @media (min-width: @tablet-width) {
    min-height: 234px;
  }

  @media (min-width: @desktop-width) {
    min-height: 243px;
  }
}

.catalog-card__image {
  position: absolute;
  inset: 0;

  margin: auto;

  @media (min-width: @tablet-width) {
    top: auto;
    bottom: 31px;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    bottom: 40px;
  }
}

.catalog-card__image--card-1 {
  width: 68px;
  height: auto;

  @media (min-width: @tablet-width) {
    width: 161px;
    height: 172px;
  }
}

.catalog-card__image--card-2 {
  @media (max-width: @mobile-width-only) {
    top: 8px;
  }

  @media (min-width: @tablet-width) {
    bottom: 25px;

    width: 168px;
    height: 206px;
  }

  @media (min-width: @desktop-width) {
    bottom: 38px;
    left: 2px;

    width: 172px;
    height: 202px;
  }
}

.catalog-card__image--card-3 {
  width: 68px;
  height: auto;

  @media (max-width: @mobile-width-only) {
    top: 17px;
  }

  @media (min-width: @tablet-width) {
    width: 163px;
    height: 174px;
  }
}

.catalog-card__image--card-4 {
  @media (max-width: @mobile-width-only) {
    top: 8px;
  }

  @media (min-width: @tablet-width) {
    width: 168px;
    height: 200px;
  }

  @media (min-width: @desktop-width) {
    bottom: 44px;
    left: 2px;

    width: 172px;
    height: 196px;
  }
}

.catalog-card__image--card-5 {
  width: 68px;
  height: auto;

  @media (max-width: @mobile-width-only) {
    top: 23px;
  }

  @media (min-width: @tablet-width) {
    width: 161px;
    height: 171px;
  }
}

.catalog-card__image--card-6 {
  @media (max-width: @mobile-width-only) {
    top: 9px;
  }

  @media (min-width: @tablet-width) {
    width: 168px;
    height: 200px;
  }

  @media (min-width: @desktop-width) {
    bottom: 44px;
    left: 2px;

    width: 172px;
    height: 196px;
  }
}

.catalog-card__image--card-7 {
  @media (max-width: @mobile-width-only) {
    top: 18px;
    right: 1px;
    width: 72px;
    height: auto;
  }

  @media (min-width: @tablet-width) {
    width: 163px;
    height: 174px;
  }
}

.catalog-card__link--title {
  grid-column: 2 / 4;
  margin-bottom: 5px;

  text-decoration: none;

  &:hover {
    opacity: 60%;
    cursor: pointer;
  }

  &:active {
    opacity: 40%;
  }

  &:focus {
    .focus();
  }

  @media (min-width: @tablet-width) {
    margin-top: 4px;
    margin-bottom: 14px;

    text-align: center;
  }

  @media (min-width: @desktop-width) {
    margin-top: 3px;
    margin-bottom: 17px;
    padding: 0 5px;
  }
}

.catalog-card__title {
  margin: 0;

  font-family: @font-family-header;
  font-size: 16px;
  line-height: 20px;
  font-weight: @font-weight-regular;
  color: @alter-header-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 24px;
  }

  @media (min-width: @desktop-width) {
    line-height: 21px;
  }
}

.catalog-card__title .space {
  //margin-right: 4px;

  @media (min-width: @tablet-width) {
    margin-right: 0;
    margin-left: 5px;
  }

  @media (min-width: @desktop-width) {
    margin-right: -5px;
    margin-left: 5px;
  }
}

.catalog-card__features {
  grid-column: 2 / 4;
  display: grid;
  align-content: start;
  grid-template-columns: 1fr 1fr;
  margin: 0;

  font-family: @font-family-baze;
  font-size: 12px;
  line-height: 16px;
  font-weight: @font-weight-regular;
  color: @baze-color;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 16px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 1px;
  }
}

.catalog-card__feature-name,
.catalog-card__feature-value {
  @media (min-width: @tablet-width) {
    padding-top: 7px;
    padding-bottom: 7px;
    border-bottom: 1px solid #cdcdcd;
  }

  @media (min-width: @desktop-width) {
    padding-top: 8px;
    padding-bottom: 7px;
  }
}

.catalog-card__feature-value {
  margin: 0;

  text-align: right;
}

.catalog-card__feature-value--mass {
  padding-right: 2px;

  @media (min-width: @desktop-width) {
    padding-right: 1px;
  }
}

.catalog-card__button {
  grid-column: 1 / -1;
  margin-top: 12px;
  padding-bottom: 9px;

  @media (min-width: @tablet-width) {
    margin-top: auto;
    margin-bottom: 39px;

    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 40px;
  }
}
