.advantages {
  @media (min-width: @tablet-width) {
    border-bottom: 1px solid #e6e6e6;
  }

  @media (min-width: @desktop-width) {
    border-bottom: none;
  }
}

.advantages__wrapper {
  padding-bottom: 49px;

  @media (min-width: @tablet-width) {
    padding-top: 25px;
    padding-bottom: 55px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 52px;
    padding-bottom: 30px;
  }
}

.advantages__title {
  margin: 0;
  margin-bottom: 40px;

  font-family: @font-family-header;
  font-size: 36px;
  line-height: 40px;
  font-weight: @font-weight-regular;
  color: @header-color;

  @media (min-width: @tablet-width) {
    margin-left: -1px;
    margin-bottom: 32px;

    font-size: 60px;
    line-height: 60px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  row-gap: 20px;
  margin: 0;
  padding: 0;

  list-style-type: none;

  @media (min-width: @tablet-width) {
    grid-template-columns: 40% 40%;
    justify-content: space-between;
    align-items: start;
    row-gap: 46px;
    padding-right: 68px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 80px;
    padding-right: 0;
  }
}

.advantages__item {
  min-height: 60px;
  padding-left: 80px;

  background-repeat: no-repeat;
  background-position: top left;
  background-size: 61px auto;

  @media (min-width: @tablet-width) {
    min-height: 100px;
    padding: 151px 8px 20px 0px;

    background-position: top 40px left, top 40px left 125px;
    background-size: 80px auto, auto;
  }

  @media (min-width: @desktop-width) {
    padding-right: 0;
  }
}

.advantages__item--useful {
  background-image: url("/img/leaf-mobile.png");

  @media (min-width: @tablet-width) {
    margin-left: -1px;
    background-image: url("/img/leaf.png"), url("/img/bg-1.png");
  }

  @media (min-width: @desktop-width) {
    margin-left: 0;
  }
}

.advantages__item--shape {
  background-image: url("/img/tub-mobile.png");

  @media (min-width: @tablet-width) {
    background-image: url("/img/tub.png"), url("/img/bg-2.png");
  }
}

.advantages__item--manual {
  background-image: url("/img/eat-mobile.png");

  @media (min-width: @tablet-width) {
    margin-left: -1px;
    background-image: url("/img/eat.png"), url("/img/bg-3.png");
  }

  @media (min-width: @desktop-width) {
    margin-left: 0;
  }
}

.advantages__item--result {
  background-image: url("/img/timer-mobile.png");

  @media (min-width: @tablet-width) {
    background-image: url("/img/timer.png"), url("/img/bg-4.png");
  }
}

.advantages__text {
  margin-top: 2px;
  margin-bottom: 4px;

  font-family: @font-family-baze;
  font-size: 14px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @baze-color;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
  }
}
