.page-footer__wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
}

.page-footer--inner {
  background-color: #f2f2f2;
}

.page-footer__visit-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 26px 20px;

  background-color: @alter-color;

  @media (min-width: @tablet-width) {
    align-items: center;
    box-sizing: border-box;
    width: 92%;
    max-width: 1220px;
    margin: 0 auto;
    padding: 58px 40px 59px;
  }

  @media (min-width: @desktop-width) {
    position: absolute;
    top: 93px;
    left: 110px;

    box-sizing: border-box;
    width: 565px;
    margin: 0;
    padding: 58px 80px 59px;

    background-color: @alter-color;
  }
}

.page-footer__visit-card--inner {
  background-color: #f2f2f2;

  @media (min-width: @desktop-width) {
    background-color: @alter-color;
  }
}

.page-footer__caption {
  margin: 0;
  padding-right: 10px;

  font-family: @font-family-header;
  font-size: 16px;
  line-height: 20px;
  font-weight: @font-weight-regular;
  color: #111111;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    padding-top: 1px;
    margin-left: -1px;

    font-size: 20px;
    line-height: 26px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 4px;
  }
}

.page-footer__contacts {
  display: flex;
  flex-direction: column;
  margin: 0;

  font-family: @font-family-baze;
  font-size: 14px;
  line-height: 20px;
  font-weight: @font-weight-regular;
  color: @baze-color;

  @media (min-width: @tablet-width) {
    row-gap: 20px;

    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }

  @media (min-width: @desktop-width) {
    padding-left: 48px;

    text-align: left;
  }
}

.page-footer__map {
  min-height: 362px;

  background-image: url("/img/map-mobile.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-width: @tablet-width) {
    min-height: 400px;

    background-image: url("/img/map-tablet.png");
  }

  @media (min-width: @desktop-width) {
    background-image: url("/img/map-desktop.png");
  }
}

.page-footer__map iframe {
  @media (min-width: @tablet-width) {
    height: 400px;
  }
}

.page-footer__set {
  padding-top: 39px;
  padding-bottom: 22px;

  background-color: #f2f2f2;

  @media (min-width: @tablet-width) {
    padding-top: 55px;
    padding-bottom: 50px;
  }
}

.page-footer__set-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: space-between;
  row-gap: 18px;

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr auto 1fr;
    align-items: center;
  }
}

.page-footer__logo-text {
  display: block;
  padding-bottom: 27px;

  text-align: center;

  border-bottom: 1px solid #d9d9d9;

  &:hover {
    opacity: 60%;
  }

  &:active {
    opacity: 40%;
  }

  &:focus {
    .focus();
  }

  @media (min-width: @tablet-width) {
    margin-right: auto;
    padding-top: 5px;
    padding-bottom: 0;

    border: none;
  }

  @media (min-width: @desktop-width) {
    padding-top: 0;
  }
}

.page-footer__social {
  padding-bottom: 18px;

  border-bottom: 1px solid #d9d9d9;

  @media (min-width: @tablet-width) {
    padding-bottom: 0;

    border: none;
  }
}
