.promo__wrapper {
  text-align: center;

  background-image: url("/img/gift.png"), url("/img/cat-bag.png");
  background-position: center 65px, center;
  background-size: 56px auto, cover;
  background-color: @primary-color;
  background-repeat: no-repeat;

  @media (min-resolution: 2dppx) {
    background-image: url("/img/gift.png"), url("/img/cat-bag@2x.png");
    background-position: center 65px, center;
  }

  @media (min-width: @tablet-width) {
    text-align: left;

    background-image: url("/img/gift.png"), url("/img/cat-bag-tablet.png");
    background-position: left 88px top 63px, center;
    background-size: 58px auto, cover;

    @media (min-resolution: 2dppx) {
      background-image: url("/img/gift.png"), url("/img/cat-bag-tablet@2x.png");
      background-position: left 88px top 63px, center;
    }
  }

  @media (min-width: @desktop-width) {
    text-align: center;

    background-image: url("/img/gift.png"), url("/img/cat-bag.png");
    background-position: center top 62px, right center;

    @media (min-resolution: 2dppx) {
      background-image: url("/img/gift.png"), url("/img/cat-bag@2x.png");
      background-position: center top 62px, right center;
    }
  }
}

.promo__text {
  margin: 0;
  padding: 187px 20px 43px;

  font-family: @font-family-baze;
  font-size: 16px;
  line-height: 20px;
  font-weight: @font-weight-regular;
  color: @alter-color;

  @media (min-width: @tablet-width) {
    width: max-content;
    margin-left: auto;
    padding: 73px 125px 67px 0;
  }

  @media (min-width: @desktop-width) {
    min-width: auto;
    margin: 0 auto;
    padding: 187px 20px 42px;
  }
}
