.wrapper {
  box-sizing: border-box;
  width: 100%;
  min-width: 280px;
  padding: 0px 20px;

  @media (min-width: @tablet-width) {
    width: 92%;
    max-width: 1220px;
    padding: 0;
    margin: 0 auto;
  }
}

.wrapper__page--form {
  @media (min-width: @tablet-width) {
    padding-left: 38px;
    padding-right: 36px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 0;
    padding-right: 0;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.bg-line {
  position: relative;

  display: grid;
  grid-template-columns: min-content max-content auto;
  column-gap: 20px;
  width: 100%;

  @media (min-width: @tablet-width) {
    grid-template-columns: 4% max-content auto;
    column-gap: 40px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 70px max-content auto;
  }
}

.bg-line::before,
.bg-line::after {
  position: static;

  width: 100%;
  height: 2px;
  margin-top: 16px;

  background-color: @primary-color;

  content: "";

  @media (min-width: @tablet-width) {
    margin-top: 22px;
  }
}

.br-mobile {
  display: block;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.br-tablet {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }
}
