.page {
  background-color: #ffffff;
}

.page__body {
  position: relative;

  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;

  font-family: @font-family-baze;
  font-size: 14px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @baze-color;
}
