.page-main__title {
  margin-top: 0;
  margin-bottom: 37px;
  padding-top: 27px;

  font-family: @font-family-header;
  font-size: 36px;
  line-height: 36px;
  font-weight: @font-weight-regular;
  color: @header-color;

  @media (min-width: @tablet-width) {
    padding-top: 49px;

    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 58px;
  }
}

.page-main--inner .page-main__title {
  padding-top: 28px;

  @media (min-width: @tablet-width) {
    padding-top: 49px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 58px;
  }
}
