.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 4px;

  @media (min-width: @tablet-width) {
    justify-content: flex-end;
  }
}

.copyright__link--image {
  @media (min-width: @tablet-width) {
    margin-left: 23px;
  }

  &:hover .copyright__logo--image {
    fill: @primary-color;
    stroke: @primary-color;
  }

  &:active .copyright__logo--image {
    fill: @primary-color;
    opacity: 40%;
  }

  &:focus {
    .focus();
  }
}

.copyright__link--text {
  &:hover {
    opacity: 60%;
  }

  &:active {
    opacity: 40%;
  }

  &:focus {
    .focus();
  }
}

.copyright__logo--image {
  fill: #666666;
}
