.page-header {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  min-height: 65px;
  padding-top: 9px;

  border-bottom: 1px solid #d9d9d9;

  @media (min-width: @tablet-width) {
    grid-template-columns: max-content max-content auto;
    min-width: 708px;
    padding-top: 25px;

    border: none;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: max-content auto 50%;
    min-width: 1220px;
    padding-top: 55px;
  }
}

.page-header__logo-cat {
  display: block;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.page-header__logo-wings {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }
}

.page-header__logo-rays {
  display: none;

  @media (min-width: @desktop-width) {
    display: block;
  }
}

.page-header__logo--image {
  &:hover {
    opacity: 60%;
  }

  &:active {
    opacity: 40%;
  }

  &:focus {
    .focus();
  }

  @media (min-width: @tablet-width) {
    margin-right: 8px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 6px;
  }
}

.page-header__image-logo {
  width: 70px;
  height: 59px;
  margin-bottom: -10px;

  @media (min-width: @tablet-width) {
    box-sizing: border-box;
    width: 61px;
    height: 50px;
    margin-bottom: 12px;
  }

  @media (min-width: @desktop-width) {
    width: 70px;
    height: 59px;
  }
}

.page-header__logo--text {
  margin-bottom: 5px;

  &:hover {
    opacity: 60%;
  }

  &:active {
    opacity: 40%;
  }

  &:focus {
    .focus();
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 9px;
  }
}

.page-header--inner .page-header__logo--text {
  margin-bottom: 0;

  @media (min-width: @tablet-width) {
    margin-bottom: 5px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 6px;
  }
}

.page-header__text-logo {
  width: 101px;
  height: 19px;
  padding: 4px;

  @media (min-width: @desktop-width) {
    width: 126px;
    height: 22px;
  }
}

.page-header__nav {
  text-align: right;

  @media (min-width: @tablet-width) {
    margin-bottom: 8px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 20px;
  }
}

.page-header--inner .page-header__nav {
  @media (min-width: @tablet-width) {
    margin-bottom: 7px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 16px;
  }
}
