@media (max-width: @mobile-width-only) {
  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--nojs {
    grid-column: 1 / -1;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__list {
    position: static;

    display: block;
  }
}

.main-nav__wrapper {
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    justify-content: flex-end;
    flex-flow: row wrap;
  }
}

.main-nav__toggle {
  position: relative;
  width: 24px;
  height: 24px;
  margin-bottom: 13px;

  background: none;
  border: none;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.main-nav__toggle--inner {
  margin-bottom: 10px;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 4px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#000000, 30%);
  box-shadow: 0 8px 0 0 fade(#000000, 30%), 0 16px 0 0 fade(#000000, 30%);
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    right: 0;
    width: 24px;
    height: 1.5px;
    background-color: #000000;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: fade(#000000, 30%);
  }
}

.main-nav__list {
  position: absolute;
  top: 65px;
  left: 0;

  width: 100%;
  min-width: 320px;
  min-height: 198px;
  margin: 0;
  padding: 0;

  background-color: @alter-color;

  @media (min-width: @tablet-width) {
    position: static;

    min-height: 40px;
  }

  @media (min-width: @desktop-width) {
    background: none;
  }
}

.main-nav__item {
  margin: 0;
  padding: 0;

  border-top: 1px solid #e6e6e6;
  background-color: #ffffff;

  z-index: 1;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    margin-left: 36px;

    background: none;
    border: none;
  }

  @media (min-width: @desktop-width) {
    margin-left: 35px;
  }
}

.main-nav__link {
  position: relative;

  display: block;
  padding-top: 22px;
  padding-bottom: 23px;

  font-family: @font-family-header;
  font-size: 20px;
  line-height: 20px;
  font-weight: @font-weight-medium;
  text-align: center;
  color: @header-color;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    opacity: 60%;
    cursor: pointer;
  }

  &:active {
    opacity: 100%;
  }

  &:focus {
    .focus();
  }

  &:disabled {
    opacity: 40%;
  }

  @media (min-width: @tablet-width) {
    padding: 9px 0;

    font-size: 18px;
    line-height: 24px;
    font-weight: @font-weight-regular;
  }

  @media (min-width: @desktop-width) {
    padding: 8px 0;

    font-size: 20px;
    line-height: 30px;
    color: @alter-color;
  }
}

.main-nav__link--active::after {
  @media (min-width: @tablet-width) {
    position: absolute;
    bottom: -1px;
    left: 0;

    width: 100%;
    height: 2px;

    background-color: @primary-color;

    content: "";
  }

  @media (min-width: @desktop-width) {
    bottom: -1px;

    background-color: @alter-color;
  }
}

.main-nav__list--inner .main-nav__link {
  color: @header-color;

  @media (min-width: @tablet-width) {
    padding: 7px 0;
  }
}

.main-nav__list--inner .main-nav__link--active::after {
  @media (min-width: @desktop-width) {
    background-color: @primary-color;
  }
}
