.example {
  background-color: #eaeaea;

  @media (min-width: @tablet-width) {
    background-image: linear-gradient(
      #ffffff 619px,
      #eaeaea 619px,
      #eaeaea 100%
    );
  }

  @media (min-width: @desktop-width) {
    background-image: linear-gradient(
      #ffffff 184px,
      #eaeaea 184px,
      #eaeaea 100%
    );
  }
}

.example__wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 11px;
  padding-top: 25px;
  padding-bottom: 33px;

  @media (min-width: @tablet-width) {
    row-gap: 80px;
    padding-top: 43px;
    padding-bottom: 53px;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    column-gap: 195px;
    padding-top: 0;
    padding-bottom: 63px;
  }
}

.example__text-block {
  display: grid;
  row-gap: 20px;

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 1fr 2fr;
    row-gap: 53px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr;
    align-content: start;
    row-gap: 0;
    width: 436px;
    padding-top: 48px;
  }
}

.example__title {
  margin: 0;
  margin-bottom: 20px;

  font-family: @font-family-header;
  font-size: 36px;
  line-height: 40px;
  font-weight: @font-weight-regular;
  color: @header-color;

  @media (min-width: @tablet-width) {
    grid-column: 1 / -1;
    margin-bottom: 12px;

    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 146px;
  }
}

.example__description {
  margin: 0;

  font-family: @font-family-baze;
  font-size: 14px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @baze-color;

  @media (min-width: @tablet-width) {
    grid-column: 1 / -1;

    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: @desktop-width) {
    br {
      display: none;
    }
  }
}

.example__list {
  grid-column: 1 / -1;

  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
  margin: 0;
  padding: 0;

  text-align: center;

  list-style-type: none;

  @media (min-width: @tablet-width) {
    grid-column: 1 / 3;
    column-gap: 42px;
    margin-left: -1px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / -1;
    grid-template-columns: 162px 162px;
    margin-top: 68px;
  }
}

.example__item {
  box-sizing: border-box;
  min-width: 124px;

  @media (min-width: @tablet-width) {
    width: 162px;
  }
}

.example__set {
  position: relative;

  margin: 0;
  padding: 13px 13px 17px;

  border: 1px solid #cdcdcd;
  border-radius: 3px;

  @media (min-width: @tablet-width) {
    padding-bottom: 19px;
  }

  @media (min-width: @desktop-width) {
    box-sizing: border-box;
    padding: 15px;
    padding-bottom: 18px;
  }
}

.example__number {
  margin: 0;

  font-family: @font-family-header;
  font-size: 24px;
  line-height: 24px;
  font-weight: @font-weight-regular;
  color: @header-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 37px;
  }
}

.example__caption {
  position: absolute;
  bottom: -7px;
  left: 50%;

  display: block;
  max-height: 35px;
  padding: 2px 14px 9px;
  overflow: hidden;

  font-family: @font-family-baze;
  font-size: 12px;
  line-height: 12px;
  font-weight: @font-weight-regular;
  color: @baze-color;
  text-align: center;

  background-color: #eaeaea;

  transform: translate(-50%, 50%);

  @media (min-width: @tablet-width) {
    bottom: 1px;
    max-width: 90%;
    padding: 11px 5px;

    font-size: 14px;
    line-height: 14px;
    white-space: nowrap;

    background-color: #ffffff;
  }

  @media (min-width: @desktop-width) {
    bottom: 0;
    background-color: #eaeaea;
  }
}

.example__result {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0;
  padding-top: 14px;
  padding-bottom: 20px;

  font-family: @font-family-header;
  font-size: 14px;
  line-height: 20px;
  font-weight: @font-weight-regular;
  color: @header-color;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    flex-direction: column;
    align-items: flex-end;
    row-gap: 10px;
    padding: 0;

    font-size: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / -1;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    column-gap: 63px;
    margin-top: 60px;
  }
}

.example__result-number {
  margin-left: 3px;

  @media (min-width: @tablet-width) {
    margin-left: 0;
  }
}

.example__picture {
  @media (min-width: @tablet-width) {
    width: 591px;
    margin: 0 auto;
  }

  @media (min-width: @tablet-width) {
    padding-top: 5px;
  }
}

.example__slider {
  display: grid;
  grid-template-columns: 1fr;
  width: auto;
  min-height: 232px;
  margin-bottom: 17px;

  @media (min-width: @tablet-width) {
    grid-template-columns: 50% 50%;
    width: auto;
    height: 511px;
    margin-bottom: 18px;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 21px;
  }
}

.example__image--active {
  display: block;
}

.example__image--inactive {
  display: none;
}

.example__image {
  width: auto;

  background-repeat: no-repeat;
  background-position: top center;
  background-size: auto 241px;

  @media (min-width: @tablet-width) {
    grid-row: 1 / -1;
    margin-bottom: 0;
  }
}

.example__image--fat {
  background-image: url("/img/cat-fat-foto.png");

  @media (min-resolution: 2dppx) {
    background-image: url("/img/cat-fat-foto@2x.png");
  }

  @media (min-width: @tablet-width) {
    grid-column: 1 / 2;
    display: block;

    background-size: 650px 497px;
    background-clip: border-box;
    background-position: top 0 right 100%;
  }
}

.example__image--slim {
  background-image: url("/img/cat-skinny-foto.png");

  @media (min-resolution: 2dppx) {
    background-image: url("/img/cat-skinny-foto@2x.png");
  }

  @media (min-width: @tablet-width) {
    grid-column: 2 / 3;
    display: block;

    background-size: 533px 509px;
    background-clip: border-box;
    background-position: top 0 left 100%;
  }
}
