.additional-cards {
  margin-bottom: 35px;

  @media (min-width: @tablet-width) {
    margin-bottom: 76px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 86px;
  }
}

.additional-cards__title {
  margin-bottom: 35px;

  font-family: @font-family-header;
  font-size: 24px;
  line-height: 24px;
  font-weight: @font-weight-regular;
  color: @header-color;

  @media (min-width: @tablet-width) {
    column-gap: 36px;
    margin-bottom: 80px;

    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: @desktop-width) {
    column-gap: 40px;
    margin-bottom: 79px;
    padding: 0;
  }
}

.additional-cards__wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 35px;

  @media (min-width: @tablet-width) {
    row-gap: 69px;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.additional-cards__list {
  display: flex;
  flex-direction: column;
  row-gap: 19px;
  margin: 0;
  padding: 0;
  padding-bottom: 18px;

  border-bottom: 1px solid #cdcdcd;

  list-style-type: none;

  @media (min-width: @tablet-width) {
    row-gap: 28px;
    padding-bottom: 27px;

    border: 1px solid #cdcdcd;
    border-top: none;
  }

  @media (min-width: @desktop-width) {
    row-gap: 16px;
    width: 895px;
    padding: 0;

    border-left: none;
    border-right: none;
  }
}

.additional-cards__item {
  border-top: 1px solid #cdcdcd;

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
  }
}

.additional-cards__promo {
  @media (min-width: @desktop-width) {
    width: 245px;
    padding: 0;
  }
}
