.catalog {
  margin-bottom: 25px;
  padding-top: 3px;
}

.catalog__wrapper {
  @media (min-width: @tablet-width) {
    box-sizing: border-box;
    width: 92%;
    max-width: 1220px;
    margin: 0 auto;
    margin-bottom: 68px;
    padding: 0;
    padding-top: 51px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 76px;
    padding-top: 45px;
  }
}

.catalog__list {
  display: grid;
  row-gap: 23px;
  margin: 0;
  padding: 0;

  list-style-type: none;

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 1fr;
    gap: 60px 65px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 60px 80px;
  }
}

.catalog__item {
  border-top: 1px solid #ebebeb;

  @media (min-width: @tablet-width) {
    border: none;
  }
}

.catalog__item:not(:last-child) {
  @media (min-width: @tablet-width) {
    background: linear-gradient(#ffffff 76px, #f2f2f2 76px, #f2f2f2 100%);
  }

  @media (min-width: @desktop-width) {
    background: linear-gradient(#ffffff 83px, #f2f2f2 83px, #f2f2f2 100%);
  }
}

.catalog__more-cards {
  box-sizing: border-box;
  width: 100%;
  min-width: 280px;
  padding: 0 20px;
  padding-top: 25px;

  text-align: center;

  border-bottom: 1px solid #ebebeb;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    min-height: 429px;
    margin-top: 77px;
    padding: 52px 38px 0;

    border: 1px solid #f2f2f2;
  }

  @media (min-width: @desktop-width) {
    margin-top: 80px;
    padding: 52px 37px 0 38px;

    min-width: 245px;
  }
}

.catalog__more-link--image {
  position: relative;

  display: block;
  width: 30px;
  height: 30px;
  margin: 0 auto;
  margin-bottom: 24px;

  &:hover {
    opacity: 60%;
    cursor: pointer;
  }

  &:active {
    opacity: 40%;
  }

  &:focus {
    .focus();
  }

  @media (min-width: @tablet-width) {
    width: 60px;
    height: 60px;
    margin-bottom: 48px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 50px;
  }
}

.catalog__more-image {
  position: absolute;
  top: 14px;

  display: block;
  width: 30px;
  height: 2px;

  background-color: #d3d3d3;

  @media (min-width: @tablet-width) {
    top: 29px;

    width: 60px;
  }
}

.catalog__more-image::after {
  position: absolute;
  top: -14px;
  left: 14px;

  display: block;
  width: 2px;
  height: 30px;

  background-color: #d3d3d3;

  content: " ";

  @media (min-width: @tablet-width) {
    top: -29px;
    left: 29px;

    height: 60px;
  }
}

.catalog__more-link--text {
  display: block;
  margin-bottom: 6px;

  text-decoration: none;

  &:hover {
    opacity: 60%;
    cursor: pointer;
  }

  &:active {
    opacity: 40%;
  }

  &:focus {
    .focus();
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 20px;
  }
}

.catalog__more-caption {
  margin-bottom: 21px;

  font-family: @font-family-header;
  font-size: 16px;
  line-height: 20px;
  font-weight: @font-weight-regular;
  color: #111111;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 22px;
  }
}

.catalog__more-caption .space {
  margin-right: 3px;

  @media (min-width: @tablet-width) {
    margin-right: 0;
  }
}

.catalog__more-subtitle {
  margin: 0;
  margin-bottom: 21px;

  font-family: @font-family-baze;
  font-size: 12px;
  line-height: 16px;
  font-weight: @font-weight-regular;
  color: @baze-color;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 18px;
  }
}

.catalog__more-subtitle .space {
  margin-right: 3px;

  @media (min-width: @tablet-width) {
    margin-right: 0;
  }
}

.catalog__more-button {
  margin-bottom: 24px;
  padding: 10px;

  color: @baze-color;

  background-color: #f2f2f2;

  &:hover {
    background-color: #ebebeb;
    cursor: pointer;
  }

  &:active {
    color: rgba(68, 68, 68, 0.3);

    background-color: #ebebeb;
  }

  &:focus {
    .focus();
  }

  &:disabled {
    opacity: 40%;
  }

  @media (min-width: @tablet-width) {
    margin-top: auto;
    margin-bottom: 37px;
    padding: 11px;

    font-size: 16px;
    line-height: 18px;
  }

  @media (min-width: @desktop-width) {
    padding: 12px 11px 8px;

    line-height: 20px;
  }
}
