.program-select {
  margin-bottom: 32px;

  @media (min-width: @tablet-width) {
    margin-bottom: 72px;
    padding-top: 18px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 82px;
  }
}

.program-select__description {
  margin-top: 0;
  margin-bottom: 39px;

  font-family: @font-family-header;
  font-size: 14px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @header-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    margin-bottom: 66px;

    font-size: 20px;
    line-height: 30px;
  }
}

.program-select__form {
  display: grid;
  margin-bottom: 21px;

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 1fr;
  }
}

.program-select__group {
  margin: 0;
  padding: 0;
}

.program-select__group--cat-data,
.program-select__group--target {
  border: none;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: @tablet-width) {
    border-bottom: 0;
  }
}

.program-select__group--contacts,
.program-select__group--comment,
.program-select__group--options {
  border: none;
}

.program-select__legend {
  margin-bottom: 35px;
  margin-left: -2px;

  font-family: @font-family-header;
  font-size: 24px;
  line-height: 26px;
  font-weight: @font-weight-regular;
  color: @header-color;

  @media (min-width: @tablet-width) {
    max-width: 100%;
    margin-bottom: 76px;
    margin-left: -4px;

    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: @desktop-width) {
    margin-left: -2px;
    margin-bottom: 75px;
  }
}

.program-select__legend br {
  @media (min-width: @tablet-width) {
    display: none;
  }
}

.program-select__list {
  margin: 0;
  padding: 0;

  list-style-type: none;

  @media (min-width: @tablet-width) {
    margin-left: 2px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 0;
  }
}

.program-select__item:not(:last-child) {
  margin-bottom: 14px;

  @media (min-width: @tablet-width) {
    margin-bottom: 20px;
  }
}

.program-select__label {
  position: relative;

  display: flex;
  align-items: center;

  font-family: @font-family-header;
  font-size: 16px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @baze-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.program-select__label--radio,
.program-select__label--checkbox {
  font-family: @font-family-header;
  font-size: 16px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @alter-header-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.program-select__input {
  box-sizing: border-box;
  padding: 13px 14px 12px;

  font-family: @font-family-header;
  font-size: 16px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @baze-color;
  text-transform: uppercase;

  border: 1px solid #e7e7e7;

  &:hover {
    border-color: #cdcdcd;
    cursor: pointer;
  }

  &:active,
  &:focus {
    border-color: #ffffff;
    outline: 2px solid #444444;
    outline-offset: 0px;
  }

  &:disabled {
    opacity: 40%;
  }

  @media (min-width: @tablet-width) {
    padding: 10px 18px;

    font-size: 20px;
    line-height: 30px;
  }
}

.program-select__input--e-mail:focus + span {
  path {
    stroke: @primary-color;
  }
}

.program-select__input--tel:focus + span {
  path {
    fill: @primary-color;
  }
}

//    
.program-select__input--invalid {
  border: 1px solid #ff8282;
}

.program-select__input--invalid + span {
  path {
    stroke: #ff8282;
  }
}

.program-select__input--invalid.program-select__input--tel + span {
  path {
    fill: #ff8282;
    stroke: none;
  }
}

.program-select__group--cat-data {
  padding-bottom: 23px;
  margin-bottom: 22px;

  @media (min-width: @tablet-width) {
    grid-column: 1 / -1;
    margin-bottom: 49px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / 2;
    width: 620px;
    margin-left: auto;
    margin-bottom: 45px;
    padding-right: 14px;
  }
}

.program-select__input--text {
  flex-grow: 1;

  margin-left: 14px;

  @media (min-width: @tablet-width) {
    margin-left: 16px;
  }
}

.program-select__input--weight {
  margin-left: 16px;
  padding-left: 15px;

  @media (min-width: @tablet-width) {
    margin-left: 20px;
    padding-left: 18px;
  }
}

.program-select__group--target {
  padding-bottom: 18px;
  margin-bottom: 26px;

  @media (min-width: @tablet-width) {
    grid-column: 1 / -1;
    margin-bottom: 57px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 / 3;
    width: 620px;
    margin-right: auto;
    margin-bottom: 51px;
    padding-left: 14px;
  }
}

.program-select__label--target {
  min-height: 30px;

  @media (min-width: @tablet-width) {
    min-height: 40px;
  }
}

.program-select__list--target {
  @media (min-width: @tablet-width) {
    padding: 24px 26px;

    border: 1px solid #e7e7e7;
  }
}

.program-select__item--target:not(:last-child) {
  margin-bottom: 13px;
}

.program-select__mark--radio,
.program-select__mark--checkbox {
  &:hover {
    border-color: #bababa;
    cursor: pointer;
  }

  &:active {
    opacity: 30%;
  }

  &:focus {
    .focus();
  }

  &:disabled {
    opacity: 30%;
  }
}

.program-select__label:focus-within .program-select__mark--radio,
.program-select__label:focus-within .program-select__mark--checkbox {
  .focus();
}

.program-select__mark--radio {
  position: absolute;
  top: 0;
  left: 0;

  width: 28px;
  height: 28px;

  background-color: #ffffff;
  border: 1px solid #cdcdcd;
  border-radius: 50%;

  @media (min-width: @tablet-width) {
    width: 39px;
    height: 39px;
  }
}

.program-select__input--radio:checked + .program-select__mark--radio::before {
  position: absolute;
  inset: 0;
  margin: auto;

  width: 14px;
  height: 14px;
  background-color: @primary-color;
  border-radius: 50%;
  content: "";

  @media (min-width: @tablet-width) {
    width: 18px;
    height: 18px;
  }
}

.program-select__label--radio {
  padding-left: 46px;

  @media (min-width: @tablet-width) {
    padding-left: 61px;
  }
}

.program-select__group--contacts {
  margin-bottom: 23px;

  @media (min-width: @tablet-width) {
    grid-column: 1 / -1;
    margin-bottom: 65px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / -1;
    margin-bottom: 67px;
  }
}

.program-select__group--contacts .space {
  margin: 0;

  @media (min-width: @tablet-width) {
    margin-right: 8px;
  }
}

.program-select__list--contacts {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 14px;

  @media (min-width: @tablet-width) {
    row-gap: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr 1fr;
    column-gap: 78px;
  }
}

.program-select__input--e-mail,
.program-select__input--tel {
  position: relative;

  flex-grow: 1;
  margin-left: 11px;

  @media (min-width: @tablet-width) {
    margin-left: 20px;
  }
}

.program-select__input--tel {
  @media (min-width: @tablet-width) {
    margin-left: 13px;
    padding-left: 19px;
  }
}

.program-select__icon {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 16px;
  margin: auto 0;
}

.program-select__icon--e-mail path {
  stroke: #000000;
}

.program-select__icon--e-mail {
  @media (min-width: @tablet-width) {
    right: 18px;
  }
}

.program-select__icon--tel {
  top: 1px;
  right: 13px;

  @media (min-width: @tablet-width) {
    right: 16px;
  }
}

.program-select__icon--tel path {
  fill: #000000;
}

.program-select__group--comment {
  margin-bottom: 19px;

  @media (min-width: @tablet-width) {
    grid-column: 1 / -1;
    margin-bottom: 80px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 85px;
  }
}

.program-select__textarea {
  box-sizing: border-box;
  width: 100%;
  min-height: 150px;
  margin-top: 4px;
  padding: 8px 12px 9px 14px;

  font-family: @font-family-header;
  font-size: 16px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @baze-color;
  text-transform: uppercase;

  border: 1px solid #e7e7e7;

  @media (min-width: @tablet-width) {
    min-height: 174px;
    margin-top: 0;
    padding: 10px 19px;

    font-size: 20px;
    line-height: 30px;
  }
}

.program-select__group--options {
  margin-bottom: 35px;

  @media (min-width: @tablet-width) {
    grid-column: 1 / -1;
    margin-top: -19px;
    margin-bottom: 55px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / -1;
    margin-bottom: 61px;
  }
}

.program-select__list--options {
  display: grid;
  row-gap: 13px;

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 1fr;
    gap: 32px 57px;
    margin-top: -20px;
    margin-left: 1px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(4, 221px);
    column-gap: 104px;
    margin-left: 0px;
  }
}

.program-select__label--checkbox {
  padding-left: 46px;

  @media (min-width: @tablet-width) {
    padding-top: 1px;
    padding-left: 59px;
  }
}

.program-select__mark--checkbox {
  position: absolute;
  top: 0;
  left: 0;

  width: 28px;
  height: 28px;

  background-color: #ffffff;
  border: 1px solid #cdcdcd;

  @media (min-width: @tablet-width) {
    box-sizing: border-box;
    width: 40px;
    height: 40px;
  }
}

.program-select__input--checkbox:checked
  + .program-select__mark--checkbox::before {
  position: absolute;
  inset: 0;
  width: 18px;
  height: 14px;
  margin: auto;

  background-image: url("/img/mark.svg");
  background-repeat: no-repeat;
  background-size: 100%;

  content: "";

  @media (min-width: @tablet-width) {
    width: 24px;
    height: 18px;
  }
}

.program-select__label--options {
  min-height: 29px;

  @media (min-width: @tablet-width) {
    min-height: 40px;
  }
}

.program-select__button-wrapper {
  padding: 0 20px;
  margin-bottom: 21px;

  @media (min-width: @tablet-width) {
    align-self: center;
    margin-left: 8%;
    margin-bottom: 0;
    padding-left: 40px;
    padding-right: 30px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 53px;
    padding-right: 42px;
  }
}

.program-select__button {
  padding: 10px;

  @media (min-width: @tablet-width) {
    padding: 16px;
  }
}

.program-select__note {
  display: block;
  padding-left: 20px;
  padding-right: 20px;

  font-family: @font-family-baze;
  font-size: 14px;
  line-height: 16px;
  font-weight: @font-weight-regular;
  text-align: center;
  color: @header-color;

  @media (min-width: @tablet-width) {
    align-self: center;
    margin-right: 8%;
    margin-bottom: 4px;
    padding-right: 40px;
    padding-left: 30px;

    font-size: 16px;
    line-height: 16px;
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0px;
    padding-left: 40px;
  }
}
