.social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 135px;
  margin: 0 auto;
  padding: 0;

  list-style-type: none;

  @media (min-width: @tablet-width) {
    width: 150px;
    margin-top: -5px;
  }
}

.social__link {
  position: relative;

  display: block;
  box-sizing: border-box;
  width: 26px;
  height: 26px;

  text-decoration: none;

  &:focus {
    .focus();
  }

  @media (min-width: @tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.social__icon {
  position: absolute;
  inset: 0;
  margin: auto;
  fill: #666666;

  &:hover {
    fill: @primary-color;
  }

  &:active {
    fill: @primary-color;
    opacity: 40%;
  }

  @media (min-width: @tablet-width) {
    transform: scale(1.4);
  }
}

.social__icon--facebook {
  margin-left: 4px;
  fill: none;

  &:hover {
    fill: none;
  }

  &:active {
    fill: none;
    opacity: 40%;
  }

  &:hover path {
    stroke: @primary-color;
  }

  &:active path {
    stroke: @primary-color;
    opacity: 40%;
  }
}

.social__icon--facebook path {
  stroke: #666666;
}

.copyright__image-link {
  padding-top: 4px;
}
