.intent__wrapper {
  margin-bottom: 20px;
}

.intent__list {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  margin: 0;
  padding: 0;

  list-style-type: none;

  @media (min-width: @tablet-width) {
    row-gap: 30px;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.intent__item {
  display: grid;
  grid-template-columns: min-content 1fr;
  box-sizing: border-box;
  padding: 21px 20px;

  background-color: #f2f2f2;

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 294px;
    padding: 77px 18px 56px 52px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 162px 1fr;
    width: 570px;
    padding: 40px 52px 59px;
    margin-bottom: 0;
  }
}

.intent__image {
  grid-column: 1 / 2;

  @media (min-width: @tablet-width) {
    grid-column: 2 / 3;
    grid-row: 1 / 4;
    justify-self: center;
    margin-left: 24px;
    margin-top: 4px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    justify-self: start;
    margin-top: 0;
    margin-left: 0;
  }
}

.intent__cat-slim {
  width: 50px;
  height: 53px;
  margin-right: 19px;
  padding-left: 2px;

  @media (min-width: @tablet-width) {
    width: 200px;
    height: 211px;
  }

  @media (min-width: @desktop-width) {
    width: 100px;
    height: 106px;
    margin-right: 0;
  }
}

.intent__cat-fat {
  width: 67px;
  height: 50px;
  margin-left: -7px;
  margin-right: 11px;
  padding-top: 3px;

  @media (min-width: @tablet-width) {
    width: 268px;
    height: 200px;
    margin-left: 8px;
    margin-right: 0;
    margin-top: 4px;
    padding-top: 10px;
  }

  @media (min-width: @desktop-width) {
    width: 134px;
    height: 100px;
    margin-top: -4px;
    margin-left: -18px;
  }
}

.intent__title {
  grid-column: 2 / 3;
  align-self: center;
  margin: 0;

  font-family: @font-family-header;
  font-size: 24px;
  line-height: 37px;
  font-weight: @font-weight-regular;
  color: @header-color;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    grid-column: 1 / 2;
    align-self: start;
    margin-bottom: 3px;
    padding-left: 0;

    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 2 / 3;
    align-self: center;
    margin-bottom: 0;
  }
}

.intent__description {
  grid-column: 1 / -1;
  margin-top: 22px;
  margin-bottom: 22px;

  font-family: @font-family-baze;
  font-size: 14px;
  line-height: 18px;
  font-weight: @font-weight-regular;
  color: @baze-color;

  @media (min-width: @tablet-width) {
    grid-column: 1 / 2;
    margin-left: -1px;

    font-size: 16px;
    line-height: 24px;
    color: @header-color;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / -1;
    margin-top: 40px;
  }
}

.intent__link {
  position: relative;

  grid-column: 1 / -1;
  display: block;
  padding-top: 20px;

  font-family: @font-family-header;
  font-size: 16px;
  line-height: 16px;
  font-weight: @font-weight-regular;
  color: @header-color;
  text-transform: uppercase;
  text-decoration: none;

  border-top: 1px solid #d9d9d9;

  &:active {
    opacity: 30%;
  }

  &:focus {
    .focus();
  }

  @media (min-width: @tablet-width) {
    grid-column: 1 / 2;
    margin-left: -1px;
    padding-top: 30px;

    font-size: 20px;
    line-height: 30px;

    border-top: none;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1 / -1;
    padding-top: 3px;
  }
}

.intent__link-arrow {
  margin-left: 11px;
  padding-bottom: 2px;

  @media (min-width: @tablet-width) {
    margin-left: 21px;
    padding-bottom: 4px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 24px;
    width: 24px;
    height: 12px;
    padding-bottom: 2px;
  }
}
