.toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 18px;
  padding: 12px 40px;

  @media (min-width: @tablet-width) {
    column-gap: 23px;
    padding: 8px 8px 8px 12px;
  }
}

.toggle__line {
  position: relative;

  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  box-sizing: border-box;
  width: 85px;
  height: 14px;
  padding: 3px 6px;

  background-color: @alter-color;
  border: 1px solid #cdcdcd;
  border-radius: 6px;

  &:hover {
    cursor: pointer;
  }

  @media (min-width: @tablet-width) {
    display: block;
    width: 426px;
    height: 6px;
    padding: 0;

    background-color: #dcdcdc;
    border: none;
    border-radius: 3px;

    &::before {
      position: absolute;
      top: -13px;
      left: 47%;

      width: 8px;
      height: 8px;
      margin: 0 auto;

      background-color: @primary-color;
      border: 12px solid #ffffff;
      border-radius: 50%;
      box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.25);

      content: "";
    }
  }
}

.toggle__line--active {
  @media (min-width: @tablet-width) {
    &::before {
      box-shadow: 0 0 1px 1px @primary-color, 0 0 5px 2px rgba(0, 0, 0, 0.25);
    }
  }
}

.toggle__slider {
  grid-column: 1 / 2;

  width: 36px;
  height: 6px;

  background-color: @primary-color;
  border: none;
  border-radius: 3px;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.toggle__lable {
  font-family: @font-family-header;
  font-size: 16px;
  line-height: 20px;
  font-weight: @font-weight-regular;
  color: @header-color;
  text-transform: uppercase;
  text-decoration: none;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
    display: block;
    border-bottom: 1px dotted #dcdcdc;

    &:hover {
      border-bottom-style: solid;

      cursor: pointer;
    }

    &:focus {
      .focus();
    }
  }
}

